/**
 * è¿”å›žæŒ‡å®šèŒƒå›´å†…çš„éšæœºæ•´æ•°ã€‚
 * @param {number} min æœ€å°å€¼
 * @param {number} max æœ€å¤§å€¼
 */
export const randomIntegerInRange = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;

/**
 * å°†æ•°å­—å››èˆäº”å…¥åˆ°æŒ‡å®šçš„å°æ•°ä½æ•°ã€‚
 * @param {number} n æ“ä½œçš„æ•°å­—
 * @param {number} decimals ç²¾ç¡®åˆ°å‡ ä½å°æ•° 
 */
export const round = (n, decimals = 0) => {
    return Number(`${Math.round(`${n}e${decimals}`)}e-${decimals}`)
}


/**
 * è¿”å›žä¸¤ä¸ªæˆ–ä¸¤ä¸ªä»¥ä¸Šæ•°å­—/æ•°å­—æ•°ç»„ä¸­å…ƒç´ ä¹‹å’Œã€‚
 * @param  {...any} arr æ“ä½œçš„æ•°ç»„ 
 */
export const sum = (...arr) => [...arr].reduce((acc, val) => acc + val, 0);


/**
 * æ ¹æ®å‡½æ•°æ˜ å°„æ¯ä¸ªå…ƒç´ ï¼Œç„¶åŽè¿”å›žæ•°ç»„çš„å’Œ
 * @param {*} arr 
 * @param {*} fn 
 */
export const sumBy = (arr, fn) =>
  arr.map(typeof fn === 'function' ? fn : val => val[fn]).reduce((acc, val) => acc + val, 0);

/**
 * å°†æ•°å­—è½¬åŒ–ä¸ºåƒåˆ†ä½æ ¼å¼
 * @param {*} num 
 */
export const toDecimalMark = num => num.toLocaleString('en-US');